import CloseButton from './close-button.vue';

export default {
  components: { CloseButton },
  props: {
    status: {
      type: String,
      default: '',
    },
    effect: {
      type: String,
      default: 'fade',
    },
    closeHtml: {
      type: String,
      default: '',
    }
  },
  computed: {
    effectName() {
      return `message-${this.effect}`;
    },
    classObject() {
      const obj = { message: true };
      const status = this.status.toLowerCase();
      if (this.status) obj[status] = true;
      return obj;
    }
  },
  data() {
    return {
      show: true
    };
  },
  methods: {
    onClose() {
      this.$emit('close', this);
      this.show = false;
    }
  }
}
