export default {
  props: {
    opened: {
      type: Boolean,
      default: false,
    },
    color: {
      type: String,
      default: '',
    },
  },
  computed: {
    classObject() {
      return {
        'navbar-toggle': true,
        change: this.opened,
      };
    },
    barStyle() {
      return {
        backgroundColor: (this.color) ? this.color : null,
      };
    }
  },
}
